<template>
  <div class="border">
    <el-form :inline="true" :model="searchForm" class="demo-form-inline">
      <el-form-item label="">
            <el-date-picker v-model="searchForm.startTime" type="datetime" placeholder=""
                            value-format="yyyy-MM-dd hh:mm:ss"></el-date-picker>~
            <el-date-picker v-model="searchForm.endTime" type="datetime" placeholder=""
                            value-format="yyyy-MM-dd hh:mm:ss"></el-date-picker>
      </el-form-item>
      <el-form-item label="APP">
        <el-select v-model="searchForm.appName" clearable placeholder="">
          <el-option v-for="item in $formatter.getSelectionOptions('appNames')" :key="item.value" :label="item.label" :value="item.value"/>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="searchForm.state" clearable placeholder="">
          <el-option v-for="item in $formatter.getSelectionOptions('fddcontractState')" :key="item.value" :label="item.label" :value="item.value"/>
        </el-select>
      </el-form-item>
      <br/>
      <el-form-item label="">
        <el-select v-model="searchForm.channel" clearable placeholder="">
          <el-option v-for="item in $formatter.getSelectionOptions('fddcontractChannel')" :key="item.value" :label="item.label" :value="item.value"/>
        </el-select>
      </el-form-item>
      <el-form-item label="">
          <el-input v-model="searchForm.applicationId" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" @click="list"></el-button>
      </el-form-item>
    </el-form>
    <el-table ref="switchTable" :data="tableData" border stripe highlight-current-row @selection-change="handleSelectionChange">
      <!--<el-table-column type="selection" width="55"/>-->
      <el-table-column prop="id" label="" header-align="center" align="center" min-width="40px"/>
      <el-table-column prop="appName" label="APP" header-align="center" align="center" min-width="80px"  show-overflow-tooltip>
        <template slot-scope="scope">
          <span>{{$formatter.simpleFormatSelection('appNames', scope.row.appName)}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="state" label="" header-align="center" align="center" min-width="80px"  show-overflow-tooltip/>
      <el-table-column prop="contractNo" label="" header-align="center" align="center" min-width="300px"  show-overflow-tooltip/>
      <el-table-column prop="applicationId" label="" header-align="center" align="center" min-width="210px"  show-overflow-tooltip/>
      <el-table-column prop="downloadUrl" label="()" header-align="center" align="center" min-width="150px"  show-overflow-tooltip/>
      <el-table-column prop="viewpdfUrl" label="()" header-align="center" align="center" min-width="150px"   show-overflow-tooltip/>
      <el-table-column prop="customerId" label="" header-align="center" align="center" min-width="300px"  show-overflow-tooltip/>
      <el-table-column prop="phoneNum" label="APP" header-align="center" align="center" min-width="150px" show-overflow-tooltip/>
      <el-table-column prop="idCardNo" label="" header-align="center" align="center" min-width="210px" show-overflow-tooltip/>
      <el-table-column prop="domicileAddress" label="" header-align="center" align="center" min-width="250px" show-overflow-tooltip/>
      <el-table-column prop="channel" label="" header-align="center" align="center" min-width="120px" show-overflow-tooltip>
        <template slot-scope="scope">
          <span>{{$formatter.simpleFormatSelection('appNames', scope.row.appName)}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="loanDays" label="" header-align="center" align="center" min-width="60px" show-overflow-tooltip/>
      <el-table-column prop="contractAmount" label="" header-align="center" align="center" min-width="60px" show-overflow-tooltip/>
      <el-table-column prop="interest" label="" header-align="center" align="center" min-width="60px" show-overflow-tooltip/>
      <el-table-column prop="signDate" label="" header-align="center" align="center" min-width="210px" show-overflow-tooltip/>
      <el-table-column prop="endDate" label="" header-align="center" align="center" min-width="210px" show-overflow-tooltip/>
      <el-table-column prop="signRate" label="" header-align="center" align="center" min-width="60px" show-overflow-tooltip/>
      <el-table-column prop="bankCard" label="" header-align="center" align="center" min-width="210px" show-overflow-tooltip/>
      <el-table-column prop="bankName" label="" header-align="center" align="center" min-width="120px" show-overflow-tooltip/>
      <el-table-column prop="customerCaId" label="()CAID" header-align="center" align="center" min-width="300px" show-overflow-tooltip/>
      <el-table-column prop="companyId" label="()ID" header-align="center" align="center" min-width="300px" show-overflow-tooltip/>
      <el-table-column prop="templateId" label="()ID" header-align="center" align="center" min-width="300px" show-overflow-tooltip/>
      <el-table-column prop="failureTimes" label="" header-align="center" align="center" min-width="60px"  show-overflow-tooltip/>
      <el-table-column prop="errorDescription" label="" header-align="center" align="center" min-width="300px" show-overflow-tooltip/>
      <el-table-column prop="createTime" label="" header-align="center" align="center" min-width="210px" show-overflow-tooltip/>
      <el-table-column prop="modifyTime" label="" header-align="center" align="center" min-width="210px" show-overflow-tooltip/>
      <el-table-column label="" header-align="center" align="center" min-width="120px" show-overflow-tooltip fixed="right">
        <template slot-scope="scope">
          <el-button icon="el-icon-edit" @click="updataFailTime(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @current-change="handleCurrentChange"
      @size-change="handleSizeChange"
      :current-page="pageIndex"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="pageSize"
      layout="->,total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
</template>

<script>
export default {
  data () {
    return {
      searchForm: {
        startTime: null,
        endTime: null,
        appName: null,
        state: null,
        channel: null,
        applicationId: null
      },
      entry: {},
      tableData: [],
      pageIndex: 1,
      pageSize: 10,
      total: 0,
      selectIds: [],
      showAddFlag: false,
      showEditFlag: false
    }
  },
  created () {
    this.list()
  },
  methods: {
    async list () {
      let params = {
        ...this.searchForm,
        pageIndex: this.pageIndex - 1,
        pageSize: this.pageSize
      }
      try {
        const res = await this.$http.post('/management/fadada-contract/page', params)
        if (res.code === '200') {
          this.tableData = res.data.rows
          this.total = res.data.total
        } else {
          this.$message.error(res.message)
        }
      } catch (err) {
        console.error(err)
      }
    },
    handleCurrentChange (currentPage) {
      this.pageIndex = currentPage
      this.list()
    },
    handleSizeChange (size) {
      this.pageSize = size
      this.list()
    },
    handleSelectionChange (val) {
      this.selectIds = []
      val.forEach(v => {
        this.selectIds.push(v.id)
      })
    },
    async updataFailTime (row) {
      try {
        const res = await this.$http.post('/management/fadada-contract/update-failure-times-zero', row.contractNo)
        if (res.code === '200') {
          this.$message.success('')
        } else {
          this.$message.error(res.message)
        }
      } catch (err) {
        console.error(err)
      }
    }
  }
}
</script>

<style scoped lang="stylus">
</style>



